.home {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    overflow: hidden;
    line-height: 1;
    height: calc(90vh);
}

.home-content {
    margin-bottom: 100px;
}

.home-title {
    align-items: start;
}

.home-title h2 {
    color: #222;
    font-weight: 700px;
    padding-bottom: 1rem;
    font-size: 1.2rem;
    transform: translateY(500px);
    animation: slideUp 0.8s ease forwards 0.5s;
}

.text-wrapper {
    color: #222;
    display: flex;
    align-items: center;
    font-size: 1rem;
    transform: translateY(500px);
    animation: slideUp 0.8s ease forwards 0.5s;
}

.text-wrapper p {
    color: #222;
    font-size: 3rem;
    font-weight: 600;
}

.icons-container {
    padding-top: 3.5rem;
    padding-bottom: 1.5rem;
    font-size: 1.8rem;
    display: flex;
    align-items: center;
}

.react {
    margin-right: 20px;
    transition: transform 0.2s ease-in-out;
    color: #00d8ff;
    /* React rengi */
}

.next {
    margin-right: 20px;
    transition: transform 0.2s ease-in-out;
    color: #222;
    /* Next.js rengi */
}

.vue {
    margin-right: 20px;
    transition: transform 0.2s ease-in-out;
    color: #41b883;
    /* Vue.js rengi */
}

.tailwind {
    margin-right: 20px;
    transition: transform 0.2s ease-in-out;
    color: #38b2ac;
    /* Tailwind CSS rengi */
}

.sass {
    margin-right: 20px;
    transition: transform 0.2s ease-in-out;
    color: #cc6699;
    /* Sass rengi */
}

.icon:hover {
    transform: scale(1.2);
}

.react:hover {
    transform: scale(1.2);
}

.next:hover {
    transform: scale(1.2);
}

.vue:hover {
    transform: scale(1.2);
}

.tailwind:hover {
    transform: scale(1.2);
}

.sass:hover {
    transform: scale(1.2);
}

.links {
    display: flex;
    color: #222;
    justify-content: flex-start;
    align-items: center;
    margin-top: 1rem;
    font-weight: 600;
    transform: translateY(500px);
    animation: slideUp 0.8s ease forwards 1.1s;
}

.link a {
    font-size: 1.2rem;
    font-weight: 700;
    color: #222;
    position: relative;
    padding-bottom: 2px;
    list-style: none;
}

.linkk a {
    font-size: 1.2rem;
    font-weight: 700;
    color: #222;
    position: relative;
    padding-bottom: 2px;
    list-style: none;
}

.link a:hover {
    color: #222;
    letter-spacing: 1.1px;
    transition: letter-spacing 0.5s ease-in-out;
    list-style: none;
    text-decoration-line: underline;
    text-decoration-thickness: 2px;
}

.linkk a:hover {
    color: #222;
    letter-spacing: 1.1px;
    transition: letter-spacing 0.5s ease-in-out;
    list-style: none;
    text-decoration: underline;
    text-decoration-line: underline;
    text-decoration-thickness: 2px;
}

.link a:hover::after {
    transition: letter-spacing 1s;
}

.divider {
    color: #222;
    font-weight: 700;
    font-size: 1.1rem;
    padding: 0 15px;
}

.scroll {
    position: relative;
    transform: translateY(500px);
    animation: slideUp 0.8s ease forwards 1.3s;
    margin-top: 0.25rem;
}

.scroll a span {
    font-size: 1.2rem;
    font-weight: 600;
    position: absolute;
    width: 150px;
    height: 42px;
    color: #fff;
    background-color: rgba(52, 152, 219, 0.9);
    border: 2px solid #fff;
    border-radius: 10px;
    box-sizing: border-box;
    padding: 11px;
    transition: all 0.4s;
}

.scroll a span:hover {
    border: 2px solid #fff;
    transition: all 0.4s;
    color: #f9f9f9;
    background-color: #0077B5
}

.scroll a span::before {
    content: '';
    position: absolute;
    top: 5px;
    left: 90%;
    width: 2px;
    height: 25px;
    background-color: #fff;
    border-radius: 30%;
    animation: scroll ease 2s infinite;
    box-sizing: border-box;
}

.scroll a span:hover::before {
    background-color: #fff;
    transition: all 0.4s;
}

.circle {
    color: #222;
    width: 340px;
    height: 340px;
    position: relative;
    background-color: #f9f9f9;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 80px;
}

.circle::before {
    content: '';
    position: absolute;
    inset: -15px 50px;
    background-color: #222;
    transition: 0.5s;
    animation: animatedCircle 10s linear infinite;
}

.circle::after {
    content: '';
    position: absolute;
    background-color: #f9f9f9;
    color: #222;
    inset: 7px;
    border-radius: 50%;
    z-index: 1;
}

.content {
    position: absolute;
    inset: 30px;
    border: 5px solid #222;
    z-index: 3;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    flex-direction: column;
}

.content img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform: 0.5s;
    text-align: center;
    pointer-events: none;
    z-index: 3;
    animation: none;
}

.content a {
    position: relative;
    background-color: rgba(52, 152, 219, 0.9);
    color: #f9f9f9;
    padding: 10px 10px;
    font-size: 1.2rem;
    font-weight: 600;
    border-radius: 15px;
    transition: 0.5s;
}

.content a:hover {
    background-color: #0077B5
}

.circle:hover::before {
    inset: -20px 0px;
}

.circle:hover img {
    opacity: 0;
}

@keyframes slideUp {
    0% {
        transform: translateY(500px);
    }
    100% {
        transform: translateY(0px);
    }
}

@keyframes fadeInOut {
    0% {
        opacity: 0;
        transform: scale(0.8);
    }
    30% {
        opacity: 1;
        transform: scale(0.8);
    }
    70% {
        opacity: 1;
        transform: scale(0.8);
    }
    100% {
        opacity: 0;
        transform: scale(0.8);
    }
}

@keyframes scroll {
    0% {
        transform: translateY();
    }
    100% {
        transform: translateY(10px);
    }
}

@keyframes animatedCircle {
    0% {
        transform: rotate(0);
    }
    100% {
        transform: rotate(360deg);
    }
}

/* Ekran genişliği 480px ve altında */

@media screen and (max-width: 480px) {
    .home {
        /* Gerekli stil değişiklikleri buraya ekleyin */
        height: auto;
        /* Yükseklik otomatik olarak ayarlanacak */
    }
    .home-title h2 {
        /* Diğer stil değişiklikleri buraya ekleyin */
        transform: translateY(0);
        /* Yüksekliği sıfıra çekiyoruz */
    }
    links {
        flex-direction: column;
        justify-content: space-around;
    }
    .links {
        display: flex;
        flex-direction: column;
    }
    .link,
    .linkk {
        margin-bottom: 10px;
    }
    .icons {
        display: flex;
        flex-direction: column;
        align-items: start;
        justify-content: center;
    }
    .divider {
        margin-bottom: 10px;
    }
    .text-wrapper,
    .scroll,
    .circle {
        /* Diğer stil değişiklikleri buraya ekleyin */
        transform: translateY(0);
        /* Yüksekliği sıfıra çekiyoruz */
        animation: none;
        /* Animasyonu kapatıyoruz */
    }
    .circle {
        width: 200px;
        height: 200px;
    }
    .content {}
    .scroll {
        margin-left: 30px;
    }
    .home-content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        /* Gerekli stil değişiklikleri buraya ekleyin */
        margin-bottom: 50px;
        /* Örneğin alt boşluğu azaltabilirsiniz */
    }
    .icons-container {
        /* Gerekli stil değişiklikleri buraya ekleyin */
        font-size: 1.5rem;
        /* Örneğin ikon boyutunu küçültebilirsiniz */
    }
}

@media screen and (max-width: 768px) {
    .home {
        flex-direction: column-reverse;
        line-height: 0.8;
    }
    .home-title h2 {
        font-size: 1.5rem;
        margin-bottom: 0.3rem;
    }
    .text-wrapper {
        font-size: 1rem;
    }
    .text-wrapper p {
        font-size: 2rem;
    }
}

@media screen and (min-width: 769px) and (max-width: 1200px) {
    .home-title h2 {
        font-size: 1.5rem;
        margin-bottom: 0.3rem;
    }
    .text-wrapper {
        font-size: 1rem;
    }
    .text-wrapper p {
        font-size: 2.5rem;
    }
}

/* Daha büyük ekranlar için varsayılan stiller */

@media screen and (min-width: 1201px) {
    .home-title h2 {
        font-size: 2rem;
        margin-bottom: 0.3rem;
    }
    .text-wrapper {
        font-size: 1.2rem;
    }
    .text-wrapper p {
        font-size: 3rem;
    }
}