/* Education.css */

.education {
    background-color: var(--bg-light2);
    padding-bottom: 5.5rem;
    padding-top: 2.5rem;
    text-align: center;
}

.title {
    font-size: 23px;
    font-weight: 6700;
    color: #222;
    margin-bottom: 2.5rem;
}

.timeline {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.timeline-item {
    align-items: center;
    justify-content: center;
    position: relative;
    padding: 20px 30px;
    border-left: 2px solid rgba(52, 152, 219, 0.8);
}

.timeline-item::before {
    content: "";
    position: absolute;
    top: 50%;
    left: -8px;
    width: 12px;
    height: 12px;
    background-color: rgba(52, 152, 219, 0.8);
    border-radius: 50%;
    transform: translate(0, -50%);
}

.education-cards {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.itu-card {
    border-radius: 10px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    padding: 10px 15px;
    text-align: left;
    width: 350px;
    transition: transform 0.3s ease-in-out;
    opacity: 0.97;
    /* Linear gradient arka plan rengi */
}

.ibm-card {
    border-radius: 10px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    padding: 10px 15px;
    text-align: left;
    width: 350px;
    transition: transform 0.3s ease-in-out;
    opacity: 0.97;
    /* Linear gradient arka plan rengi */
}

.ibm-card:hover,
.itu-card:hover {
    transform: scale(1.05);
}

.field {
    color: rgba(52, 152, 219, 0.9);
    font-size: 18px;
    margin: 10px 0;
    font-weight: 600;
}

.school {
    color: rgba(52, 152, 219, 0.9);
    font-weight: 600;
    font-size: 18px;
    margin-top: 5px;
    margin-bottom: 5px;
}

.date,
.location {
    font-size: 1rem;
    font-weight: 600;
    color: #444;
    margin: 5px 0;
}

.ibm-logo,
.itu-logo {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin: 20px 0;
    object-fit: cover;
    border: 4px solid #fff;
    transition: border 0.3s ease-in-out;
}

.ibm-card:hover .ibm-logo,
.itu-card:hover .itu-logo {
    border-color: #3498db;
}

/* Hover efekti eklenmiş border */

@media (max-width: 768px) {
    .education-cards {
        flex-direction: column;
        align-items: center;
    }
    .ibm-card,
    .itu-card {
        max-width: 100%;
    }
}